<template>
  <div class="pdf-watermark">
    <div class="watermark-container">
      <!-- PDF预览区域 -->
      <div class="pdf-preview-section">
        <el-card class="preview-card">
          <template #header>
            <div class="card-header">
              <span>PDF预览</span>
              <div class="preview-controls">
                <el-button-group>
                  <el-button 
                    :disabled="currentPage <= 1" 
                    @click="prevPage"
                    size="small"
                  >
                    上一页
                  </el-button>
                  <el-button size="small">
                    {{ currentPage }} / {{ totalPages }}
                  </el-button>
                  <el-button 
                    :disabled="currentPage >= totalPages" 
                    @click="nextPage"
                    size="small"
                  >
                    下一页
                  </el-button>
                </el-button-group>
                <el-button 
                  @click="resetZoom" 
                  size="small"
                  style="margin-left: 10px;"
                >
                  重置缩放
                </el-button>
              </div>
            </div>
          </template>
          
          <div class="pdf-upload-area" v-if="!pdfDocument">
            <el-upload
              ref="uploadRef"
              :auto-upload="false"
              :limit="1"
              :on-change="handleFileChange"
              :before-upload="beforeUpload"
              accept=".pdf"
              drag
              class="pdf-upload"
            >
              <el-icon class="el-icon--upload"><upload-filled /></el-icon>
              <div class="el-upload__text">
                将PDF文件拖到此处，或<em>点击上传</em>
              </div>
              <template #tip>
                <div class="el-upload__tip">
                  只能上传PDF文件，且不超过100MB
                </div>
              </template>
            </el-upload>
          </div>
          
          <div v-else class="pdf-viewer-container">
            <!-- PDF预览区域 -->
            <div 
              ref="pdfViewer" 
              class="pdf-viewer"
              @mousedown="handleMouseDown"
              @mousemove="handleMouseMove"
              @mouseup="handleMouseUp"
              @mouseleave="handleMouseUp"
            >
              <canvas ref="pdfCanvas" class="pdf-canvas" v-if="pdfDocument"></canvas>
              
              <!-- 水印预览层 -->
              <!-- 文字水印预览 -->
              <div 
                v-if="form.watermarkType === 'text' && form.watermarkText && pdfDocument"
                class="watermark-preview text-watermark"
                :style="watermarkStyle"
                @mousedown="startDrag"
              >
                {{ form.watermarkText }}
              </div>
              
              <!-- 图片水印预览 -->
              <div 
                v-if="form.watermarkType === 'image' && watermarkFile && pdfDocument"
                class="watermark-preview image-watermark"
                :style="imageWatermarkStyle"
                @mousedown="startDrag"
              >
                <img 
                  :src="watermarkImageUrl" 
                  :alt="'水印图片'"
                  class="watermark-image"
                />
              </div>
            </div>
            
            <!-- 备用预览提示 -->
            <div v-if="!pdfDocument" class="pdf-placeholder">
              <el-icon size="48"><Document /></el-icon>
              <p>PDF预览功能加载中...</p>
              <p class="placeholder-tip">如果长时间无法加载，请刷新页面重试</p>
              <el-button @click="retryLoadPdf" type="primary" size="small" style="margin-top: 10px;">
                重试加载
              </el-button>
            </div>
          </div>
        </el-card>
      </div>

      <!-- 水印设置面板 -->
      <div class="watermark-settings-section">
        <el-card class="settings-card">
          <template #header>
            <div class="card-header">
              <span>水印设置</span>
              <el-icon><Edit /></el-icon>
            </div>
          </template>
          
          <el-form :model="form" label-width="100px" size="small">
            <el-form-item label="水印类型" required>
              <el-radio-group v-model="form.watermarkType" @change="handleWatermarkTypeChange">
                <el-radio label="text">文字水印</el-radio>
                <el-radio label="image">图片水印</el-radio>
              </el-radio-group>
            </el-form-item>

            <!-- 文字水印设置 -->
            <template v-if="form.watermarkType === 'text'">
              <el-form-item label="水印文字" required>
                <el-input
                  v-model="form.watermarkText"
                  placeholder="请输入水印文字"
                  clearable
                />
              </el-form-item>

              <el-form-item label="文字颜色">
                <el-color-picker v-model="form.textColor" />
                <span class="color-preview" :style="{ backgroundColor: form.textColor }"></span>
              </el-form-item>

              <el-form-item label="字体大小">
                <el-slider
                  v-model="form.fontSize"
                  :min="12"
                  :max="120"
                  :step="2"
                  show-input
                  :format-tooltip="formatFontSize"
                />
              </el-form-item>

              <el-form-item label="文字角度">
                <el-slider
                  v-model="form.rotation"
                  :min="0"
                  :max="360"
                  :step="15"
                  show-input
                  :format-tooltip="formatRotation"
                />
              </el-form-item>

              <el-form-item label="透明度">
                <el-slider
                  v-model="form.opacity"
                  :min="0.1"
                  :max="1"
                  :step="0.1"
                  show-input
                  :format-tooltip="formatOpacity"
                />
              </el-form-item>
            </template>

            <!-- 图片水印设置 -->
            <template v-if="form.watermarkType === 'image'">
              <el-form-item label="水印图片" required>
                <el-upload
                  ref="watermarkUploadRef"
                  :auto-upload="false"
                  :limit="1"
                  :on-change="handleWatermarkFileChange"
                  :before-upload="beforeWatermarkUpload"
                  accept=".jpg,.jpeg,.png,.bmp,.tiff"
                  drag
                >
                  <el-icon class="el-icon--upload"><upload-filled /></el-icon>
                  <div class="el-upload__text">
                    将水印图片拖到此处，或<em>点击上传</em>
                  </div>
                  <template #tip>
                    <div class="el-upload__tip">
                      支持JPG、PNG、BMP、TIFF格式的图片
                    </div>
                  </template>
                </el-upload>
              </el-form-item>
            </template>

            <el-form-item>
              <el-button
                type="primary"
                :loading="loading"
                :disabled="!canSubmit"
                @click="handleSubmit"
                size="small"
              >
                {{ loading ? '处理中...' : '添加水印' }}
              </el-button>
              <el-button @click="resetForm" size="small">重置</el-button>
            </el-form-item>
          </el-form>

          <!-- 结果显示 -->
          <div v-if="result" class="result-section">
            <el-divider>处理结果</el-divider>
            <el-alert
              :title="result.msg"
              type="success"
              :closable="false"
              show-icon
            />
            <div class="result-info">
              <p><strong>任务ID:</strong> {{ result.data.taskId }}</p>
              <p><strong>处理文件:</strong> {{ result.data.watermarkedFile }}</p>
              <el-button type="success" @click="downloadResult" size="small">
                <el-icon><Download /></el-icon>
                下载结果
              </el-button>
            </div>
          </div>
        </el-card>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, reactive, computed, onMounted, nextTick } from 'vue'
import { ElMessage } from 'element-plus'
import { Edit, UploadFilled, Download } from '@element-plus/icons-vue'
import { pdfApi } from '@/api'
import { validateFile, FILE_TYPES } from '@/utils/file'
// 使用CDN版本的PDF.js以避免兼容性问题
let pdfjsLib = null

// 异步加载PDF.js
const loadPdfJs = async () => {
  if (!pdfjsLib) {
    // 检查是否已经加载了PDF.js
    if (window.pdfjsLib) {
      pdfjsLib = window.pdfjsLib
      pdfjsLib.GlobalWorkerOptions.workerSrc = 'https://cdnjs.cloudflare.com/ajax/libs/pdf.js/2.11.338/pdf.worker.min.js'
      return pdfjsLib
    }
    
    // 动态加载PDF.js CDN版本 - 使用更稳定的2.11.338版本
    const script = document.createElement('script')
    script.src = 'https://cdnjs.cloudflare.com/ajax/libs/pdf.js/2.11.338/pdf.min.js'
    script.async = true
    
    return new Promise((resolve, reject) => {
      script.onload = () => {
        pdfjsLib = window.pdfjsLib
        // 设置PDF.js worker
        pdfjsLib.GlobalWorkerOptions.workerSrc = 'https://cdnjs.cloudflare.com/ajax/libs/pdf.js/2.11.338/pdf.worker.min.js'
        resolve(pdfjsLib)
      }
      script.onerror = (error) => {
        console.error('PDF.js加载失败:', error)
        reject(error)
      }
      document.head.appendChild(script)
    })
  }
  return pdfjsLib
}

const uploadRef = ref()
const watermarkUploadRef = ref()
const pdfCanvas = ref()
const pdfViewer = ref()
const loading = ref(false)
const selectedFile = ref(null)
const watermarkFile = ref(null)
const watermarkImageUrl = ref('')
const result = ref(null)

// PDF相关状态
const pdfDocument = ref(null)
const currentPage = ref(1)
const totalPages = ref(0)
const scale = ref(1.0)
const isDragging = ref(false)
const dragStart = ref({ x: 0, y: 0 })
const watermarkPosition = ref({ x: 50, y: 50 }) // 水印位置（百分比）

const form = reactive({
  watermarkType: 'text',
  watermarkText: '',
  position: 'center',
  opacity: 0.5,
  textColor: '#FF0000',
  fontSize: 48,
  rotation: 0,
  offsetX: 0,
  offsetY: 0
})

// 计算是否可以提交
const canSubmit = computed(() => {
  if (!selectedFile.value) return false
  if (form.watermarkType === 'text' && !form.watermarkText.trim()) return false
  if (form.watermarkType === 'image' && !watermarkFile.value) return false
  return true
})

// 水印样式计算属性
const watermarkStyle = computed(() => {
  if (form.watermarkType !== 'text' || !form.watermarkText) return {}
  
  return {
    position: 'absolute',
    left: `${watermarkPosition.value.x}%`,
    top: `${watermarkPosition.value.y}%`,
    transform: `translate(-50%, -50%) rotate(${form.rotation}deg)`,
    color: form.textColor,
    fontSize: `${form.fontSize}px`,
    opacity: form.opacity,
    pointerEvents: 'auto',
    userSelect: 'none',
    whiteSpace: 'nowrap',
    zIndex: 10,
    fontFamily: 'Arial, sans-serif',
    fontWeight: 'bold',
    textShadow: '1px 1px 2px rgba(0,0,0,0.3)',
    cursor: 'move'
  }
})

// 图片水印样式计算属性
const imageWatermarkStyle = computed(() => {
  if (form.watermarkType !== 'image' || !watermarkFile.value) return {}
  
  return {
    position: 'absolute',
    left: `${watermarkPosition.value.x}%`,
    top: `${watermarkPosition.value.y}%`,
    transform: `translate(-50%, -50%) rotate(${form.rotation}deg)`,
    opacity: form.opacity,
    pointerEvents: 'auto',
    userSelect: 'none',
    zIndex: 10,
    cursor: 'move',
    maxWidth: '200px',
    maxHeight: '200px'
  }
})

// 水印类型改变处理
const handleWatermarkTypeChange = () => {
  form.watermarkText = ''
  watermarkFile.value = null
  watermarkImageUrl.value = ''
  watermarkUploadRef.value?.clearFiles()
}

// 文件选择处理
const handleFileChange = async (file) => {
  const validation = validateFile(file.raw, 'PDF')
  if (!validation.valid) {
    ElMessage.error(validation.errors[0])
    uploadRef.value.clearFiles()
    return
  }
  selectedFile.value = file.raw
  await loadPdf(file.raw)
}

// 加载PDF文件
const loadPdf = async (file) => {
  try {
    // 确保PDF.js已加载
    const pdfjs = await loadPdfJs()
    
    if (!pdfjs) {
      throw new Error('PDF.js库加载失败')
    }
    
    const arrayBuffer = await file.arrayBuffer()
    const pdf = await pdfjs.getDocument(arrayBuffer).promise
    
    pdfDocument.value = pdf
    totalPages.value = pdf.numPages
    currentPage.value = 1
    
    await renderPage(1)
    ElMessage.success('PDF加载成功')
  } catch (error) {
    console.error('PDF加载失败:', error)
    if (error.message.includes('PDF.js库加载失败')) {
      ElMessage.error('PDF预览功能暂时不可用，请稍后重试')
    } else {
      ElMessage.error('PDF加载失败，请检查文件格式')
    }
  }
}

// 渲染PDF页面
const renderPage = async (pageNum) => {
  if (!pdfDocument.value) return
  
  try {
    const page = await pdfDocument.value.getPage(pageNum)
    const canvas = pdfCanvas.value
    const context = canvas.getContext('2d')
    
    // 计算缩放比例
    const viewport = page.getViewport({ scale: scale.value })
    canvas.height = viewport.height
    canvas.width = viewport.width
    
    // 渲染页面
    const renderContext = {
      canvasContext: context,
      viewport: viewport
    }
    
    await page.render(renderContext).promise
  } catch (error) {
    console.error('页面渲染失败:', error)
    
    // 如果是私有成员访问错误，尝试重新加载PDF.js
    if (error.message && error.message.includes('private member')) {
      console.log('检测到PDF.js版本兼容性问题，尝试重新加载...')
      pdfjsLib = null
      window.pdfjsLib = null
      
      // 清除已加载的脚本
      const existingScript = document.querySelector('script[src*="pdf.min.js"]')
      if (existingScript) {
        existingScript.remove()
      }
      
      // 重新加载PDF
      try {
        await loadPdf(selectedFile.value)
      } catch (retryError) {
        console.error('重新加载失败:', retryError)
        ElMessage.error('PDF预览功能暂时不可用，请刷新页面重试')
      }
    } else {
      ElMessage.error('页面渲染失败')
    }
  }
}

// 上一页
const prevPage = async () => {
  if (currentPage.value > 1) {
    currentPage.value--
    await renderPage(currentPage.value)
  }
}

// 下一页
const nextPage = async () => {
  if (currentPage.value < totalPages.value) {
    currentPage.value++
    await renderPage(currentPage.value)
  }
}

// 重置缩放
const resetZoom = async () => {
  scale.value = 1.0
  await renderPage(currentPage.value)
}

// 重试加载PDF
const retryLoadPdf = async () => {
  if (selectedFile.value) {
    // 清除当前状态
    pdfDocument.value = null
    pdfjsLib = null
    window.pdfjsLib = null
    
    // 清除已加载的脚本
    const existingScript = document.querySelector('script[src*="pdf.min.js"]')
    if (existingScript) {
      existingScript.remove()
    }
    
    // 重新加载
    await loadPdf(selectedFile.value)
  }
}

// 拖拽相关方法
const startDrag = (event) => {
  event.preventDefault()
  isDragging.value = true
  dragStart.value = {
    x: event.clientX,
    y: event.clientY
  }
  document.addEventListener('mousemove', handleDrag)
  document.addEventListener('mouseup', stopDrag)
}

const handleDrag = (event) => {
  if (!isDragging.value) return
  
  const rect = pdfViewer.value.getBoundingClientRect()
  const x = ((event.clientX - rect.left) / rect.width) * 100
  const y = ((event.clientY - rect.top) / rect.height) * 100
  
  // 限制在PDF区域内
  watermarkPosition.value = {
    x: Math.max(0, Math.min(100, x)),
    y: Math.max(0, Math.min(100, y))
  }
}

const stopDrag = () => {
  isDragging.value = false
  document.removeEventListener('mousemove', handleDrag)
  document.removeEventListener('mouseup', stopDrag)
}

// 鼠标事件处理（用于PDF画布上的拖拽）
const handleMouseDown = (event) => {
  if ((form.watermarkType === 'text' && form.watermarkText) || 
      (form.watermarkType === 'image' && watermarkFile.value)) {
    startDrag(event)
  }
}

const handleMouseMove = (event) => {
  if (isDragging.value) {
    handleDrag(event)
  }
}

const handleMouseUp = () => {
  stopDrag()
}

// 水印文件选择处理
const handleWatermarkFileChange = (file) => {
  const validation = validateFile(file.raw, 'IMAGE')
  if (!validation.valid) {
    ElMessage.error(validation.errors[0])
    watermarkUploadRef.value.clearFiles()
    return
  }
  watermarkFile.value = file.raw
  
  // 创建图片预览URL
  const reader = new FileReader()
  reader.onload = (e) => {
    watermarkImageUrl.value = e.target.result
  }
  reader.readAsDataURL(file.raw)
}

// 上传前验证
const beforeUpload = (file) => {
  const validation = validateFile(file, 'PDF')
  if (!validation.valid) {
    ElMessage.error(validation.errors[0])
    return false
  }
  return false // 阻止自动上传
}

// 水印文件上传前验证
const beforeWatermarkUpload = (file) => {
  const validation = validateFile(file, 'IMAGE')
  if (!validation.valid) {
    ElMessage.error(validation.errors[0])
    return false
  }
  return false // 阻止自动上传
}

// 透明度格式化
const formatOpacity = (value) => {
  return `${Math.round(value * 100)}%`
}

// 字体大小格式化
const formatFontSize = (value) => {
  return `${value}px`
}

// 角度格式化
const formatRotation = (value) => {
  return `${value}°`
}

// 提交处理
const handleSubmit = async () => {
  if (!canSubmit.value) {
    ElMessage.warning('请完善所有必填项')
    return
  }

  loading.value = true
  try {
    const formData = new FormData()
    formData.append('file', selectedFile.value)
    formData.append('watermarkType', form.watermarkType)
    formData.append('opacity', form.opacity.toString())

    if (form.watermarkType === 'text') {
      formData.append('watermarkText', form.watermarkText)
      formData.append('textColor', form.textColor)
      formData.append('fontSize', form.fontSize.toString())
      formData.append('rotation', form.rotation.toString())
      
      // 使用拖拽位置而不是预设位置
      formData.append('position', 'custom')
      formData.append('offsetX', watermarkPosition.value.x.toString())
      formData.append('offsetY', watermarkPosition.value.y.toString())
    } else if (form.watermarkType === 'image' && watermarkFile.value) {
      // 图片水印：添加图片文件
      formData.append('watermarkFile', watermarkFile.value)
      formData.append('position', 'custom')
      formData.append('offsetX', watermarkPosition.value.x.toString())
      formData.append('offsetY', watermarkPosition.value.y.toString())
      formData.append('rotation', form.rotation.toString())
    }

    const response = await pdfApi.addWatermark(formData)
    result.value = response
    ElMessage.success('PDF水印添加成功')
  } catch (error) {
    console.error('PDF水印添加失败:', error)
    ElMessage.error('PDF水印添加失败，请重试')
  } finally {
    loading.value = false
  }
}

// 下载结果
const downloadResult = () => {
  if (result.value?.data?.downloadUrl) {
    try {
      // 在开发环境中，直接使用相对路径，因为代理已经配置好了
      // 在生产环境中，使用完整URL
      let downloadUrl
      if (process.env.NODE_ENV === 'development') {
        downloadUrl = result.value.data.downloadUrl
      } else {
        const apiBaseUrl = process.env.VUE_APP_BASE_API || 'http://localhost:5000/api'
        downloadUrl = `${apiBaseUrl.replace('/api', '')}${result.value.data.downloadUrl}`
      }
      
      console.log('下载URL:', downloadUrl) // 调试日志
      console.log('环境:', process.env.NODE_ENV) // 调试日志
      
      const link = document.createElement('a')
      link.href = downloadUrl
      link.download = result.value.data.watermarkedFile
      link.target = '_blank'
      link.click()
      
      ElMessage.success('下载已开始')
    } catch (error) {
      console.error('下载失败:', error)
      ElMessage.error('下载失败，请重试')
    }
  } else {
    ElMessage.warning('没有可下载的文件')
  }
}

// 重置表单
const resetForm = () => {
  form.watermarkType = 'text'
  form.watermarkText = ''
  form.position = 'center'
  form.opacity = 0.5
  form.textColor = '#FF0000'
  form.fontSize = 48
  form.rotation = 0
  form.offsetX = 0
  form.offsetY = 0
  selectedFile.value = null
  watermarkFile.value = null
  watermarkImageUrl.value = ''
  result.value = null
  pdfDocument.value = null
  currentPage.value = 1
  totalPages.value = 0
  watermarkPosition.value = { x: 50, y: 50 }
  uploadRef.value?.clearFiles()
  watermarkUploadRef.value?.clearFiles()
}
</script>

<style scoped>
.pdf-watermark {
  max-width: 1400px;
  margin: 0 auto;
  padding: 20px;
}

.watermark-container {
  display: flex;
  gap: 20px;
  height: calc(100vh - 100px);
}

.pdf-preview-section {
  flex: 2;
  min-width: 0;
}

.watermark-settings-section {
  flex: 1;
  min-width: 300px;
}

.preview-card,
.settings-card {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.preview-card .el-card__body {
  flex: 1;
  display: flex;
  flex-direction: column;
  padding: 0;
}

.settings-card .el-card__body {
  flex: 1;
  overflow-y: auto;
  padding: 20px;
}

.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 16px;
  font-weight: bold;
}

.preview-controls {
  display: flex;
  align-items: center;
  gap: 10px;
}

.pdf-upload-area {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 40px;
}

.pdf-upload {
  width: 100%;
}

.pdf-viewer-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  position: relative;
  overflow: auto;
  background-color: #f5f5f5;
  padding: 20px;
}

.pdf-viewer {
  position: relative;
  display: inline-block;
  margin: 0 auto;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  background-color: white;
  cursor: grab;
}

.pdf-viewer:active {
  cursor: grabbing;
}

.pdf-canvas {
  display: block;
  max-width: 100%;
  height: auto;
}

.watermark-preview {
  position: absolute;
  pointer-events: auto;
  cursor: move;
  user-select: none;
  z-index: 10;
  border: 2px dashed #409eff;
  border-radius: 4px;
  background-color: rgba(255, 255, 255, 0.8);
  transition: all 0.2s ease;
